.container {
    border-radius: 2px;
    padding: 0px;
}

.profile-image{
    width: 50px;
    height: 50px;
    border-radius: 25px;
}


ul{
    list-style: none;
}

#frame .content .message ul li {
  display: inline-block;
  clear: both;
  float: left;
  margin: 15px 15px 5px 15px;
  width: calc(100% - 25px);
  font-size: 0.9em;

  height: auto;
  min-height: calc(100% - 93px);
  max-height: calc(100% - 93px);
  overflow-y: scroll;
  overflow-x: hidden;
}
li.sent{
    background: #435f7a;
    color: #f5f5f5;
}
li.replies{
    background: #f5f5f5;
    float: right;
}

.message-text{
    color: white; 
    cursor: pointer;
     padding: 12px;
      font-size: 15px;
       white-space: pre-line;
        background-color: rgb(24, 144, 255);
         border-radius: 1.3em;
}

.chat-header{
    text-align: center;
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: transparent;
    font-size: 24px;
    font-weight: 600;
    background-color: bisque;
}

.profile-image{


width: 30px;
height: 35px;
border-radius: 13px;

background-repeat: no-repeat;
background-position: center center;
background-size: 60px;
}

.message-date-text{
    width: 100%; 
    text-align: center; 
    padding-top: 4px; 
    padding-bottom: 10px; 
    font-size: 15px; 
    color: rgba(0, 0, 0, 0.4);
}

.chat-bubble{
    padding: 18px 14px;
    background: #eee;
    border-radius: 9px;
    position: relative;
    animation: forwards  ease-in;
}
.right{
    color: #435f7a;
}

#msg-form-container{
    position: absolute; bottom: 0px; width: 100%; background-color: white;

}

#send-button{
    color: white; border: none; outline: none; height: 36px; font-size: 15px; cursor: pointer; padding: 8px 16px; border-radius: 33px; background-color: rgb(24, 144, 255); position: absolute; bottom: 10px; right: 6px;
}




